

//    Material Costomizations --------------------------------------------------
@use '@angular/material' as mat;

// Font faces
@font-face {
  font-family: "Univers-Condensed";
  font-style: normal;
  font-weight: normal;
  src: local("Univers-Condensed"),
    url("./fonts/Univers-Condensed.otf") format("truetype");
}

// Material theme setup
$typography: mat.define-typography-config($font-family: "Univers-Condensed");

// Define custom palettes with required hues
$primary-palette: (
  50: lighten(#29abe2, 40%),
  100: lighten(#29abe2, 30%),
  500: #29abe2,
  700: darken(#29abe2, 10%),
  contrast: (
    50: rgba(black, 0.87),
    100: rgba(black, 0.87),
    500: white,
    700: white,
  )
);

$accent-palette: (
  50: lighten(#ffc627, 40%),
  100: lighten(#ffc627, 30%),
  500: #ffc627,
  700: darken(#ffc627, 10%),
  contrast: (
    50: rgba(black, 0.87),
    100: rgba(black, 0.87),
    500: rgba(black, 0.87),
    700: rgba(black, 0.87),
  )
);

$warn-palette: (
  50: lighten(#ec2b7b, 40%),
  100: lighten(#ec2b7b, 30%),
  500: #ec2b7b,
  700: darken(#ec2b7b, 10%),
  contrast: (
    50: rgba(black, 0.87),
    100: rgba(black, 0.87),
    500: white,
    700: white,
  )
);

$theme: mat.define-light-theme((
  color: (
    primary: mat.define-palette($primary-palette),
    accent: mat.define-palette($accent-palette),
    warn: mat.define-palette($warn-palette)
  ),
  typography: $typography
));

@include mat.core();
@include mat.all-component-themes($theme);

// Component overrides
.mat-mdc-raised-button,
.mat-mdc-stroked-button,
.mat-mdc-flat-button {
  font-family: "Univers-Condensed";
}
// Specific component overrides, pieces that are not in line with the general theming

// Handle buttons appropriately, with respect to line-height
.mat-raised-button, .mat-stroked-button, .mat-flat-button {
  padding: 0 1.15em;
  margin: 0 .65em;
  min-width: 3em;
  line-height: 36.4px
}

.mat-standard-chip {
  padding: .5em .85em;
  min-height: 2.5em;
}

//    BS Costomizations --------------------------------------------------
$primary: #29abe2;
$success: #38b34a;
$info: #ec2b7b;
$warning: #ffc627;
$secondary: #414142;
$light: #eceff1;

@import "bootstrap/scss/bootstrap.scss";
@import "~@fortawesome/fontawesome-pro/css/all.min.css";

@font-face {
  font-family: "Univers-Condensed";
  font-style: normal;
  font-weight: normal;
  src: local("Univers-Condensed"),
    url("./fonts/Univers-Condensed.otf") format("truetype");
}

@font-face {
  font-family: "Univers-CondensedBold";
  font-style: normal;
  font-weight: bold;
  src: local("Univers-CondensedBold"),
    url("./fonts/Univers-CondensedBold.otf") format("truetype");
}

body {
  font-family: "Univers-Condensed", serif;
  font-size: 15.8px;
}

:root {
  --folder: rgb(255, 166, 0);
  --folder-light: rgba(253, 201, 105, 0.774);
  --bg-light-one: #929497;
  --bg-light-two: #d1d8db;
}

@keyframes scroll {
  to {
    background-position: -500px 0;
  }
}

.img-cloud {
  background-image: url("./assets/body-bg.jpg");
  background-size: 100vw;
  // background-repeat: no-repeat !important;
  background-repeat: repeat no-repeat !important;
  animation: scroll 50s linear infinite;
}

.no-link {
  text-decoration: none !important;
  color: white;
}

.table > * > * > * {
  padding: 0.3rem 0.3rem !important;
  font-size: 0.92em;
}

table {
  margin-bottom: 0;
}

thead th {
  position: -webkit-sticky;
  /* for Safari */
  position: sticky;
  top: 0;
}

thead th:first-child {
  left: 0;
  z-index: 1;
}

tbody th {
  position: -webkit-sticky;
  /* for Safari */
  position: sticky;
  left: 0;
  background: #fff;
}

.table > :not(:last-child) > :last-child > * {
  border-bottom-color: var(--border);
}

.mat-dialog-container {
  padding: 0px !important;
}

html,
body {
  height: 100%;
}

.clickable {
  cursor: pointer !important;
}
